.square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 32px;
    line-height: 80px;
    height: 80px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 80px;
}

.square:focus {
    outline: none;
}


